@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  display: flex;
  flex-direction: column;
}

.App-page {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: calc(10px + 2vmin);
  color: black;
}

.h2-light {
  font-weight: 500;
}

.h4-light {
  font-weight: 300;
}
.Navigation {
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
}

.Home-page {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: 90vh;
}

.Description {
    max-width: 50%;
}
.About-page {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    margin-top: 32px;
    /* border: 2px solid black; */
}
  
.About-page * { 
    /* border: 2px solid black; */
}

.lhs {
    flex-basis: 25%;
    margin-right: 32px;
}

.lhs * {
    margin-bottom: 16px;
}

.headshot {
    height: 70%;
    width: 70%;
}

.fa-icons {
    min-width: 32px;
    min-height: 32px;
    margin-right: 16px;
}

.rhs {
    flex-basis: 40%;
}

ul {
    margin: 0;
    padding: 0;
    padding-left: 16px;
}
.Project {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex-basis: 40%;
    min-width: 256px;
    margin-bottom: 48px;
}

.card-link{
    position: absolute;
    top: 0; left: 0;
    height: 100%; width: 100%;
}
.Projects {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 32px;
}

